img{
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0;
}

.container{
    width: 100vw;
    height: auto;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.contantsLeft{
    width: 320px;
    height: 400px;
    margin-right: 50px;
}

.contantsRight{
    width: 100% ;
    font-size: 1.2rem;
    text-align: left;
}

.contantsRight li{
    margin-bottom: 10px;
}

.skillBox{
    display: flex;
    flex-direction: row;
    margin-top: 10px;
    padding: 0;
}

.skillBox li{
    width: 5rem;
    height: 5rem;
    margin-right: 10px;
    font-weight: 600;
}

@media screen and (max-width: 1023px) {
    .container{
        flex-direction: column;
    }

    .contantsLeft{
        margin: 0;
    }

    .contantsRight{
        width: 500px;
        font-size: 1.4rem;
    }

    .skillBox li{
        width: 3rem;
        height: 3rem;
        margin-right: 10px;
    }
}

@media screen and (max-width: 767px) {
    .contantsLeft{
        margin: 0;
    }
    
    .contantsRight{
        width: 320px;
        font-size: 1rem;
    }

    .skillBox li{
        width: 1.5rem;
        height: 1.5rem;
        margin-right: 10px;
    }
}