*{
    margin:0;
    padding:0;
    color: white;
    font-family: Poppins, Pretendard, "sans-serif";
}

body{
    /* background-color: #303030; */
    background-color: black;
}

ul,li{
    list-style: none !important;
}

a{
    color: white;
    text-decoration: none !important;
}

a:hover{
    text-decoration: none !important;
}

/* @media screen and (min-width: 1024px) {
    nav{
        height: 70px;
    }
} */

nav{
    width: 100vw;
    height: 70px;
    padding: 0 36px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    text-align: center;
    z-index: 3;
    position : fixed;
    top : 0;
    left : 0;
    right : 0;
}

nav:hover{
    background-color: #303030;
}

nav ul{
    width: auto;
    height : 70px;
    align-items : center;
    padding : 0 36px;
    z-index : 3;
    display : flex;
    flex-direction: row;
}

nav ul li{
    margin: 0 35px;
}

.logo{
    width: 120px;
    height: auto;
    margin: 0;
    padding: 0;
    display: flex;
    justify-content: center;
    align-items: center;
}

.logo a{
    width: 100%;
    height: auto;
}

.logo svg{
    width: 100%;
    display: block;
}

@media screen and (max-width: 1023px) {
    nav{
        height: 50px;
        padding: 0 20px;
    }

    .logo svg{
        height: 50px;
    }

    nav ul{
        height: auto;
    }

    nav ul li{
        margin: 0 15px;
    }
}

@media screen and (max-width: 767px) {
    nav{
        height: 40px;
        /* padding: 0 20px; */
    }

    .logo svg{
        height: 40px;
    }

    nav ul{
        height: auto;
    }

    nav ul li{
        margin: 0 15px;
    }
}