img{
  object-fit: contain;
}

.project-intro {
  width: 100vw;
  height: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  color: white;
}

.project-intro h2 {
  text-align: center;
}

.project-items {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px; /* 프로젝트 아이템 사이의 간격 설정 */
}

.project-item {
  width: calc(33.33% - 20px); /* 한 줄에 3개씩 배치하기 위한 너비 설정 */
  max-width: 400px;
  height: auto;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  text-align: center;
  position: relative;
  margin-bottom: 20px;
}

.project-item img {
  /* max-width: 100%; */
  width: 400px;
  margin-bottom: 10px;
}

.project-text {
  width: 100%;
  font-size: 18px;
  margin: 0;
  display: none; /* 초기에는 숨겨둠 */
  position: absolute; /* project-item을 기준으로 위치 조정 */
  top: 50%; /* 부모 요소(project-item)의 상단에서 절반 위치로 이동 */
  left: 50%; /* 부모 요소(project-item)의 왼쪽에서 절반 위치로 이동 */
  transform: translate(-50%, -50%); /* 가운데 정렬 */
  background-color: rgba(0, 0, 0, 0.8); /* 배경색 및 투명도 설정 */
  color: white; /* 텍스트 색상 설정 */
  padding: 20px; /* 내용과 가장자리 사이 여백 설정 */
  border-radius: 5px; /* 테두리 둥글게 설정 */
}

.project-item:hover .project-text {
  display: block; /* 호버 시 project-text 표시 */
}

@media screen and (max-width: 767px) {
  .project-item {
    width: calc(50% - 20px); /* 한 줄에 2개씩 배치하기 위한 너비 설정 */
  }
}