img{
    object-fit: cover;
    border: none;
}

.banner{
    width: 100%;
    height: 700px;
    display: flex;
    justify-content: center;
    align-items: center;
    object-fit: cover;
    position: relative;
}

.banner img{
    position: absolute;
    z-index: -1;
}

.backImg{
        width: 100%;
}

/* 회전 애니메이션 적용 */
@keyframes rotate {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
}

.moon{
    width: 700px;
    animation: rotate 200s linear infinite;
}

.banner span {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: 5em;
    font-weight: bold;
    z-index: 1;
  }
  
  .banner p {
    font-size: 1em;
    margin-top: 100px;
  }

@media screen and (max-width: 1023px) {
    .banner{
        height: 430px;
    }

    .moon{
        width: 430px;
    }
}

@media screen and (max-width: 767px) {
    .banner{
        height: 270px;
    }
    .moon{
        width: 270px;
    }
}